import React, { useState, useEffect } from 'react';
import { useApp } from '../../contexts/AppContext';
import { clientAPI } from '../../services/api';
import styles from './EditClientModal.module.css';

const EditClientModal = ({ client, isOpen, onClose, onSuccess }) => {
  const { actions } = useApp();
  const [formData, setFormData] = useState({
    name: '',
    caseType: '',
    caseDate: '',
    phone: '',
    email: '',
    address: '',
    notes: ''
  });
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    if (client && isOpen) {
      setFormData({
        name: client.name || '',
        caseType: client.caseType || '',
        caseDate: client.caseDate || '',
        phone: client.phone || '',
        email: client.email || '',
        address: client.address || '',
        notes: client.notes || ''
      });
    }
  }, [client, isOpen]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!formData.name.trim()) {
      actions.showNotification('', '', 'error');
      return;
    }

    setIsSubmitting(true);
    try {
      // API
      await clientAPI.updateClient(client.cid, {
        name: formData.name,
        caseType: formData.caseType,
        caseDate: formData.caseDate,
        phone: formData.phone,
        email: formData.email,
        address: formData.address,
        notes: formData.notes
      });
      
      actions.showNotification('', '', 'success');
      onSuccess();
      onClose();
    } catch (error) {
      actions.showNotification('', error.message || '', 'error');
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  if (!isOpen) return null;

  return (
    <div className={styles.modalOverlay}>
      <div className={styles.modal}>
        <div className={styles.modalHeader}>
          <h3></h3>
          <button 
            className={styles.closeButton}
            onClick={onClose}
            disabled={isSubmitting}
          >
            
          </button>
        </div>
        
        <form onSubmit={handleSubmit} className={styles.modalContent}>
          <div className={styles.formGrid}>
            <div className={styles.formGroup}>
              <label> *</label>
              <input
                type="text"
                name="name"
                value={formData.name}
                onChange={handleChange}
                className={styles.input}
                placeholder=""
                required
              />
            </div>
            
            <div className={styles.formGroup}>
              <label></label>
              <input
                type="text"
                name="caseType"
                value={formData.caseType}
                onChange={handleChange}
                className={styles.input}
                placeholder=""
              />
            </div>
            
            <div className={styles.formGroup}>
              <label></label>
              <input
                type="date"
                name="caseDate"
                value={formData.caseDate}
                onChange={handleChange}
                className={styles.input}
              />
            </div>
            
            <div className={styles.formGroup}>
              <label></label>
              <input
                type="tel"
                name="phone"
                value={formData.phone}
                onChange={handleChange}
                className={styles.input}
                placeholder=""
              />
            </div>
            
            <div className={styles.formGroup}>
              <label></label>
              <input
                type="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                className={styles.input}
                placeholder=""
              />
            </div>
            
            <div className={styles.formGroup}>
              <label></label>
              <input
                type="text"
                name="address"
                value={formData.address}
                onChange={handleChange}
                className={styles.input}
                placeholder=""
              />
            </div>
            
            <div className={styles.formGroup} style={{gridColumn: '1 / -1'}}>
              <label></label>
              <textarea
                name="notes"
                value={formData.notes}
                onChange={handleChange}
                className={styles.textarea}
                placeholder=""
                rows={3}
              />
            </div>
          </div>
          
          <div className={styles.modalFooter}>
            <button 
              type="button"
              className={styles.cancelButton}
              onClick={onClose}
              disabled={isSubmitting}
            >
              
            </button>
            <button 
              type="submit"
              className={styles.saveButton}
              disabled={isSubmitting}
            >
              {isSubmitting ? '...' : ''}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default EditClientModal;