import React, { useState } from 'react';
import { useApp } from '../../contexts/AppContext';
import { clientAPI } from '../../services/api';
import styles from './Modal.module.css';

const AddClientModal = () => {
  const { state, actions } = useApp();
  const { modals } = state;
  const [formData, setFormData] = useState({
    name: '',
    caseType: '',
    caseDate: new Date().toISOString().split('T')[0]
  });
  const [loading, setLoading] = useState(false);

  const handleClose = () => {
    actions.toggleModal('addClient', false);
    setFormData({
      name: '',
      caseType: '',
      caseDate: new Date().toISOString().split('T')[0]
    });
  };

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!formData.name.trim()) {
      actions.showNotification('', '', 'error');
      return;
    }

    setLoading(true);

    try {
      // API
      const response = await clientAPI.addClient({
        name: formData.name.trim(),
        caseType: formData.caseType.trim(),
        caseDate: formData.caseDate
      });
      actions.addClient(response.client);

      actions.showNotification('', ` ${formData.name} `, 'success');
      handleClose();
      
    } catch (error) {
      actions.showNotification('', error.message || '', 'error');
    } finally {
      setLoading(false);
    }
  };

  if (!modals.addClient) return null;

  return (
    <div className={styles.overlay} onClick={handleClose}>
      <div className={styles.modal} onClick={(e) => e.stopPropagation()}>
        <div className={styles.header}>
          <h3 className={styles.title}></h3>
          <button className={styles.closeBtn} onClick={handleClose}>
            &times;
          </button>
        </div>
        
        <div className={styles.body}>
          <form onSubmit={handleSubmit}>
            <div className={styles.formGroup}>
              <label className={styles.label} htmlFor="name">
                 *
              </label>
              <input
                type="text"
                id="name"
                name="name"
                className={styles.input}
                placeholder=""
                value={formData.name}
                onChange={handleChange}
                required
              />
            </div>
            
            <div className={styles.formGroup}>
              <label className={styles.label} htmlFor="caseType">
                
              </label>
              <input
                type="text"
                id="caseType"
                name="caseType"
                className={styles.input}
                placeholder=""
                value={formData.caseType}
                onChange={handleChange}
              />
            </div>
            
            <div className={styles.formGroup}>
              <label className={styles.label} htmlFor="caseDate">
                
              </label>
              <input
                type="date"
                id="caseDate"
                name="caseDate"
                className={styles.input}
                value={formData.caseDate}
                onChange={handleChange}
              />
            </div>
          </form>
        </div>
        
        <div className={styles.footer}>
          <button 
            className={`${styles.btn} ${styles.btnSecondary}`}
            onClick={handleClose}
            disabled={loading}
          >
            
          </button>
          <button 
            className={`${styles.btn} ${styles.btnPrimary}`}
            onClick={handleSubmit}
            disabled={loading}
          >
            {loading ? '...' : ''}
          </button>
        </div>
      </div>
    </div>
  );
};

export default AddClientModal;





