import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useApp } from '../contexts/AppContext';
import { clientAPI } from '../services/api';
import Header from '../components/common/Header';
import AddClientModal from '../components/modals/AddClientModal';
import EditClientModal from '../components/modals/EditClientModal';
import ConfirmDialog from '../components/common/ConfirmDialog';
import styles from './DashboardPage.module.css';

const DashboardPage = () => {
  const navigate = useNavigate();
  const { state, actions } = useApp();
  const { user, clients } = state;
  const [loading, setLoading] = useState(true);
  const [editingClient, setEditingClient] = useState(null);
  const [showEditModal, setShowEditModal] = useState(false);
  const [confirmDialog, setConfirmDialog] = useState({ show: false, type: '', client: null });
  const [openMenuId, setOpenMenuId] = useState(null);

  useEffect(() => {
    loadClients();
  }, []);

  // 
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (openMenuId && !event.target.closest(`.${styles.menuContainer}`)) {
        setOpenMenuId(null);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [openMenuId]);

  const loadClients = async () => {
    try {
      setLoading(true);
      // API
      const clientsData = await clientAPI.getClients();
      actions.setClients(clientsData.clients || []);
    } catch (error) {
      actions.showNotification('', '', 'error');
    } finally {
      setLoading(false);
    }
  };

  const handleClientClick = (client) => {
    actions.setCurrentClient(client);
    navigate(`/client/${client.cid}`);
  };

  const handleAddClient = () => {
    actions.toggleModal('addClient', true);
  };

  const handleDeleteClient = (client, e) => {
    e.stopPropagation(); // 
    
    setConfirmDialog({
      show: true,
      type: 'delete',
      client,
      title: '',
      message: ` "${client.name}" `,
      confirmText: '',
      cancelText: ''
    });
    setOpenMenuId(null);
  };

  // 
  const handleEditClient = (client, e) => {
    e.stopPropagation();
    setEditingClient(client);
    setShowEditModal(true);
    setOpenMenuId(null);
  };

  // 
  const handleArchiveClient = (client, e) => {
    e.stopPropagation();
    setConfirmDialog({
      show: true,
      type: 'archive',
      client,
      title: '',
      message: ` "${client.name}" `,
      confirmText: '',
      cancelText: ''
    });
    setOpenMenuId(null);
  };

  // 
  const toggleMenu = (clientId, e) => {
    e.stopPropagation();
    setOpenMenuId(openMenuId === clientId ? null : clientId);
  };

  // 
  const handleConfirm = async () => {
    const { type, client } = confirmDialog;
    
    try {
      if (type === 'archive') {
        await clientAPI.archiveClient(client.cid, '');
        actions.showNotification('', ` ${client.name} `, 'success');
      } else if (type === 'delete') {
        await clientAPI.deleteClient(client.cid);
        actions.showNotification('', ` ${client.name} `, 'success');
      }
      
      loadClients();
    } catch (error) {
      const actionName = type === 'archive' ? '' : '';
      actions.showNotification(`${actionName}`, error.message || `${actionName}`, 'error');
    }
    
    setConfirmDialog({ show: false, type: '', client: null });
  };

  // 
  const handleCloseEditModal = () => {
    setShowEditModal(false);
    setEditingClient(null);
  };

  // 
  const handleEditSuccess = () => {
    loadClients();
  };

  if (loading) {
    return (
      <div className={styles.loadingContainer}>
        <div className="loading-spinner"></div>
        <p>...</p>
      </div>
    );
  }

  return (
    <div className={styles.dashboard}>
      <Header />

      <div className={styles.content}>
        <div className={styles.clientsSection}>
          <div className={styles.sectionHeader}>
            <h2 className={styles.sectionTitle}></h2>
            <button 
              className={styles.addClientBtn}
              onClick={handleAddClient}
            >
              
            </button>
          </div>

          {clients.length === 0 ? (
            <div className={styles.emptyState}>
              <div className={styles.emptyIcon}></div>
              <h3 className={styles.emptyTitle}></h3>
              <p className={styles.emptyDescription}></p>
              <button 
                className={styles.addClientBtn}
                onClick={handleAddClient}
              >
                
              </button>
            </div>
          ) : (
            <div className={styles.clientsList}>
              {clients.map((client) => (
                <div
                  key={client.cid}
                  className={styles.clientCard}
                  onClick={() => handleClientClick(client)}
                >
                  <div className={styles.clientContent}>
                    <div className={styles.clientName}>{client.name}</div>
                    <div className={styles.clientMeta}>
                      <span>: {client.caseType || ''}</span>
                      <span>: {client.caseDate}</span>
                    </div>
                  </div>
                  <div className={styles.menuContainer}>
                    <button
                      className={styles.menuBtn}
                      onClick={(e) => toggleMenu(client.cid, e)}
                      title=""
                    >
                      
                    </button>
                    {openMenuId === client.cid && (
                      <div className={styles.dropdownMenu}>
                        <button
                          className={styles.menuItem}
                          onClick={(e) => handleEditClient(client, e)}
                        >
                          
                        </button>
                        <button
                          className={`${styles.menuItem} ${styles.archiveItem}`}
                          onClick={(e) => handleArchiveClient(client, e)}
                        >
                          
                        </button>
                        <button
                          className={`${styles.menuItem} ${styles.deleteItem}`}
                          onClick={(e) => handleDeleteClient(client, e)}
                        >
                          
                        </button>
                      </div>
                    )}
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>

      <AddClientModal />
      
      {showEditModal && editingClient && (
        <EditClientModal
          client={editingClient}
          isOpen={showEditModal}
          onClose={handleCloseEditModal}
          onSuccess={handleEditSuccess}
        />
      )}
      
      {confirmDialog.show && (
        <ConfirmDialog
          title={confirmDialog.title}
          message={confirmDialog.message}
          confirmText={confirmDialog.confirmText}
          cancelText={confirmDialog.cancelText}
          onConfirm={handleConfirm}
          onCancel={() => setConfirmDialog({ show: false, type: '', client: null })}
          isDestructive={confirmDialog.type === 'delete'}
        />
      )}
    </div>
  );
};

export default DashboardPage;





