import React, { useState, useEffect } from 'react';
import { useApp } from '../../contexts/AppContext';
import { materialAPI } from '../../services/api';
import styles from './LaTeXEditModal.module.css';

const LaTeXEditModal = ({ isOpen, onClose, material }) => {
  const { actions } = useApp();
  const [editMode, setEditMode] = useState('description'); // 'description' | 'code'
  const [description, setDescription] = useState('');
  const [latexCode, setLatexCode] = useState('');
  const [loading, setLoading] = useState(false);
  const [history, setHistory] = useState([]);
  const [currentHistoryIndex, setCurrentHistoryIndex] = useState(-1);

  // LaTeX
  const mockLatexCode = `\\documentclass{article}
\\usepackage[utf8]{inputenc}
\\usepackage{graphicx}
\\usepackage{booktabs}
\\usepackage{xeCJK}

\\title{}
\\author{}
\\date{\\today}

\\begin{document}

\\maketitle

\\section{}

LaTeX

\\begin{table}[h]
\\centering
\\begin{tabular}{@{}lll@{}}
\\toprule
 &  &  \\\\
\\midrule
 & PDF &  \\\\
 & OCR &  \\\\
\\bottomrule
\\end{tabular}
\\caption{}
\\end{table}

\\section{}



\\end{document}`;

  useEffect(() => {
    if (isOpen && material) {
      // LaTeX
      setLatexCode(mockLatexCode);
      setDescription('');
      
      // 
      const initialHistory = [{
        timestamp: new Date().toISOString(),
        description: '',
        code: mockLatexCode
      }];
      setHistory(initialHistory);
      setCurrentHistoryIndex(0);
    }
  }, [isOpen, material]);

  const handleClose = () => {
    setEditMode('description');
    setDescription('');
    setLatexCode('');
    setHistory([]);
    setCurrentHistoryIndex(-1);
    onClose();
  };

  const handleDescriptionEdit = async () => {
    if (!description.trim()) {
      actions.showNotification('', '', 'warning');
      return;
    }

    setLoading(true);
    try {
      // AILaTeX
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      // 
      const newCode = latexCode.replace(
        '',
        '\\n\\n' + description + '\\n'
      );
      
      // 
      const newHistoryItem = {
        timestamp: new Date().toISOString(),
        description: description,
        code: newCode
      };
      
      const newHistory = [...history, newHistoryItem];
      setHistory(newHistory);
      setCurrentHistoryIndex(newHistory.length - 1);
      setLatexCode(newCode);
      setDescription('');
      
      actions.showNotification('', 'LaTeX', 'success');
      
      // API
      // const result = await materialAPI.editLatex(material.id, description);
      // setLatexCode(result.latexCode);
      
    } catch (error) {
      actions.showNotification('', error.message || '', 'error');
    } finally {
      setLoading(false);
    }
  };

  const handleCodeEdit = () => {
    if (latexCode.trim() === (history[currentHistoryIndex]?.code || '')) {
      actions.showNotification('', '', 'warning');
      return;
    }

    // 
    const newHistoryItem = {
      timestamp: new Date().toISOString(),
      description: '',
      code: latexCode
    };
    
    const newHistory = [...history, newHistoryItem];
    setHistory(newHistory);
    setCurrentHistoryIndex(newHistory.length - 1);
    
    actions.showNotification('', '', 'success');
  };

  const handleHistorySelect = (index) => {
    setCurrentHistoryIndex(index);
    setLatexCode(history[index].code);
  };

  const handleApply = () => {
    // LaTeX
    actions.updateMaterial(material.id, { 
      latexCode: latexCode,
      lastModified: new Date().toISOString()
    });
    
    actions.showNotification('', 'LaTeX', 'success');
    handleClose();
  };

  if (!isOpen) return null;

  return (
    <div className={styles.overlay} onClick={handleClose}>
      <div className={styles.modal} onClick={(e) => e.stopPropagation()}>
        <div className={styles.header}>
          <h3 className={styles.title}>
            LaTeX  - {material?.name}
          </h3>
          <button className={styles.closeBtn} onClick={handleClose}>
            &times;
          </button>
        </div>

        <div className={styles.toolbar}>
          <div className={styles.modeSwitch}>
            <button 
              className={`${styles.modeBtn} ${editMode === 'description' ? styles.active : ''}`}
              onClick={() => setEditMode('description')}
            >
               AI
            </button>
            <button 
              className={`${styles.modeBtn} ${editMode === 'code' ? styles.active : ''}`}
              onClick={() => setEditMode('code')}
            >
               
            </button>
          </div>
          
          <div className={styles.actions}>
            <button 
              className={`${styles.btn} ${styles.btnPrimary}`}
              onClick={handleApply}
            >
              
            </button>
          </div>
        </div>

        <div className={styles.body}>
          <div className={styles.leftPanel}>
            {editMode === 'description' ? (
              <div className={styles.descriptionMode}>
                <h4 className={styles.panelTitle}></h4>
                <textarea
                  className={styles.descriptionInput}
                  placeholder="&#10;- &#10;- &#10;- &#10;- "
                  value={description}
                  onChange={(e) => setDescription(e.target.value)}
                />
                <button 
                  className={`${styles.btn} ${styles.btnSuccess}`}
                  onClick={handleDescriptionEdit}
                  disabled={loading || !description.trim()}
                >
                  {loading ? ' AI...' : ' '}
                </button>
              </div>
            ) : (
              <div className={styles.codeMode}>
                <div className={styles.codeHeader}>
                  <h4 className={styles.panelTitle}>LaTeX </h4>
                  <button 
                    className={`${styles.btn} ${styles.btnSecondary}`}
                    onClick={handleCodeEdit}
                  >
                    
                  </button>
                </div>
                <textarea
                  className={styles.codeEditor}
                  value={latexCode}
                  onChange={(e) => setLatexCode(e.target.value)}
                  placeholder="LaTeX..."
                />
              </div>
            )}
          </div>

          <div className={styles.rightPanel}>
            <h4 className={styles.panelTitle}></h4>
            <div className={styles.historyList}>
              {history.map((item, index) => (
                <div 
                  key={index}
                  className={`${styles.historyItem} ${
                    index === currentHistoryIndex ? styles.active : ''
                  }`}
                  onClick={() => handleHistorySelect(index)}
                >
                  <div className={styles.historyHeader}>
                    <span className={styles.historyIndex}>#{index + 1}</span>
                    <span className={styles.historyTime}>
                      {new Date(item.timestamp).toLocaleTimeString()}
                    </span>
                  </div>
                  <div className={styles.historyDescription}>
                    {item.description}
                  </div>
                </div>
              ))}
            </div>
            
            <div className={styles.previewSection}>
              <h4 className={styles.panelTitle}></h4>
              <div className={styles.preview}>
                <div className={styles.previewPlaceholder}>
                  <div className="loading-spinner"></div>
                  <p>LaTeX </p>
                  <small>PDF</small>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LaTeXEditModal;




