import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useApp } from '../contexts/AppContext';
import { clientAPI } from '../services/api';
import Header from '../components/common/Header';
import ConfirmDialog from '../components/common/ConfirmDialog';
import styles from './DashboardPage.module.css'; //  DashboardPage 

const ArchivedClientsPage = () => {
  const navigate = useNavigate();
  const { state, actions } = useApp();
  const { user } = state;
  const [loading, setLoading] = useState(true);
  const [archivedClients, setArchivedClients] = useState([]);
  const [confirmDialog, setConfirmDialog] = useState({ show: false, type: '', client: null });
  const [openMenuId, setOpenMenuId] = useState(null);

  useEffect(() => {
    loadArchivedClients();
  }, []);

  // 
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (openMenuId && !event.target.closest(`.${styles.menuContainer}`)) {
        setOpenMenuId(null);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [openMenuId]);

  const loadArchivedClients = async () => {
    try {
      setLoading(true);
      // 
      const response = await fetch('/api/clients?include_archived=true', {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('auth_token')}`,
          'Content-Type': 'application/json',
        },
      });
      const clientsData = await response.json();
      // 
      const archived = (clientsData.clients || []).filter(client => client.isArchived);
      setArchivedClients(archived);
    } catch (error) {
      actions.showNotification('', '', 'error');
    } finally {
      setLoading(false);
    }
  };

  const handleClientClick = (client) => {
    actions.setCurrentClient(client);
    navigate(`/client/${client.cid}`);
  };

  const handleUnarchiveClient = (client, e) => {
    e.stopPropagation();
    setConfirmDialog({
      show: true,
      type: 'unarchive',
      client,
      title: '',
      message: ` "${client.name}" `,
      confirmText: '',
      cancelText: ''
    });
    setOpenMenuId(null);
  };

  const handleDeleteClient = (client, e) => {
    e.stopPropagation();
    setConfirmDialog({
      show: true,
      type: 'delete',
      client,
      title: '',
      message: ` "${client.name}" `,
      confirmText: '',
      cancelText: ''
    });
    setOpenMenuId(null);
  };

  // 
  const toggleMenu = (clientId, e) => {
    e.stopPropagation();
    setOpenMenuId(openMenuId === clientId ? null : clientId);
  };

  // 
  const handleConfirm = async () => {
    const { type, client } = confirmDialog;
    
    try {
      if (type === 'unarchive') {
        await clientAPI.unarchiveClient(client.cid);
        actions.showNotification('', ` ${client.name} `, 'success');
      } else if (type === 'delete') {
        await clientAPI.deleteClient(client.cid);
        actions.showNotification('', ` ${client.name} `, 'success');
      }
      
      loadArchivedClients();
    } catch (error) {
      const actionName = type === 'unarchive' ? '' : '';
      actions.showNotification(`${actionName}`, error.message || `${actionName}`, 'error');
    }
    
    setConfirmDialog({ show: false, type: '', client: null });
  };

  if (loading) {
    return (
      <div className={styles.loadingContainer}>
        <div className="loading-spinner"></div>
        <p>...</p>
      </div>
    );
  }

  return (
    <div className={styles.dashboard}>
      <Header />

      <div className={styles.content}>
        <div className={styles.clientsSection}>
          <div className={styles.sectionHeader}>
            <h2 className={styles.sectionTitle}></h2>
            <button 
              className={styles.addClientBtn}
              onClick={() => navigate('/dashboard')}
            >
              
            </button>
          </div>

          {archivedClients.length === 0 ? (
            <div className={styles.emptyState}>
              <div className={styles.emptyIcon}></div>
              <h3 className={styles.emptyTitle}></h3>
              <p className={styles.emptyDescription}></p>
              <button 
                className={styles.addClientBtn}
                onClick={() => navigate('/dashboard')}
              >
                
              </button>
            </div>
          ) : (
            <div className={styles.clientsList}>
              {archivedClients.map((client) => (
                <div
                  key={client.cid}
                  className={`${styles.clientCard} ${styles.archivedCard}`}
                  onClick={() => handleClientClick(client)}
                >
                  <div className={styles.clientContent}>
                    <div className={styles.clientName}>
                      {client.name}
                      <span className={styles.archivedBadge}></span>
                    </div>
                    <div className={styles.clientMeta}>
                      <span>: {client.caseType || ''}</span>
                      <span>: {client.caseDate}</span>
                      <span>: {client.archivedAt ? new Date(client.archivedAt).toLocaleDateString() : ''}</span>
                      {client.archivedReason && <span>: {client.archivedReason}</span>}
                    </div>
                  </div>
                  <div className={styles.menuContainer}>
                    <button
                      className={styles.menuBtn}
                      onClick={(e) => toggleMenu(client.cid, e)}
                      title=""
                    >
                      
                    </button>
                    {openMenuId === client.cid && (
                      <div className={styles.dropdownMenu}>
                        <button
                          className={styles.menuItem}
                          onClick={(e) => handleUnarchiveClient(client, e)}
                        >
                          
                        </button>
                        <button
                          className={`${styles.menuItem} ${styles.deleteItem}`}
                          onClick={(e) => handleDeleteClient(client, e)}
                        >
                          
                        </button>
                      </div>
                    )}
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
      
      {confirmDialog.show && (
        <ConfirmDialog
          title={confirmDialog.title}
          message={confirmDialog.message}
          confirmText={confirmDialog.confirmText}
          cancelText={confirmDialog.cancelText}
          onConfirm={handleConfirm}
          onCancel={() => setConfirmDialog({ show: false, type: '', client: null })}
          isDestructive={confirmDialog.type === 'delete'}
        />
      )}
    </div>
  );
};

export default ArchivedClientsPage;