import React, { useState, useRef } from 'react';
import FabricImageEditor from '../components/translation/FabricImageEditor';
import previewStyles from '../components/translation/PreviewSection.module.css';
import './PDFTestPage.css';

/**
 * PDF
 * FabricImageEditor
 */
const PDFTestPage = () => {
  const [pdfData, setPdfData] = useState(null);
  const [currentPageIndex, setCurrentPageIndex] = useState(0);
  const [pages, setPages] = useState([]);
  const [isUploading, setIsUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState('');
  const [notification, setNotification] = useState(null);
  const [refreshKey, setRefreshKey] = useState(0); // 

  // 
  const pageEditsRef = useRef({});

  const showNotification = (message, type = 'info') => {
    setNotification({ message, type });
    setTimeout(() => setNotification(null), 3000);
  };

  /**
   * PDF
   */
  const handleFileUpload = async (file) => {
    if (!file || file.type !== 'application/pdf') {
      showNotification('PDF', 'error');
      return;
    }

    setIsUploading(true);
    setUploadProgress('PDF...');

    const formData = new FormData();
    formData.append('pdf_file', file);

    try {
      const response = await fetch('/api/pdf/split-pages', {
        method: 'POST',
        body: formData
      });

      if (!response.ok) {
        throw new Error('');
      }

      setUploadProgress('...');

      const data = await response.json();

      if (data.success) {
        setPdfData(data);
        setPages(data.pages);

        // 
        const edits = {};
        data.pages.forEach((page, idx) => {
          edits[idx] = {
            originalUrl: page.image_url,
            regions: [],
            hasEdits: false
          };
        });
        pageEditsRef.current = edits;

        setCurrentPageIndex(0);
        showNotification(`  ${data.total_pages} `, 'success');
      } else {
        throw new Error(data.error || '');
      }
    } catch (error) {
      showNotification(': ' + error.message, 'error');
    } finally {
      setIsUploading(false);
      setUploadProgress('');
    }
  };

  /**
   * 
   */
  const saveCurrentPage = async () => {
    if (!window.currentFabricEditor || !window.currentFabricEditor.generateBothVersions) {
      return true;
    }

    try {
      const result = await window.currentFabricEditor.generateBothVersions();

      if (result) {
        pageEditsRef.current[currentPageIndex] = {
          ...pageEditsRef.current[currentPageIndex],
          editedBlob: result.edited.blob,
          finalBlob: result.final.blob,
          regions: result.edited.regions,
          hasEdits: true
        };

        console.log(`  ${currentPageIndex + 1} `);
        return true;
      }

      return false;
    } catch (error) {
      console.error(':', error);
      return false;
    }
  };

  /**
   * 
   */
  const goToPage = async (pageIndex) => {
    if (pageIndex < 0 || pageIndex >= pages.length) return;
    if (pageIndex === currentPageIndex) return;

    // 
    await saveCurrentPage();

    // 
    setCurrentPageIndex(pageIndex);
    showNotification(` ${pageIndex + 1} `, 'info');
  };

  /**
   * 
   */
  const previousPage = async () => {
    if (currentPageIndex > 0) {
      await goToPage(currentPageIndex - 1);
    }
  };

  /**
   * 
   */
  const nextPage = async () => {
    if (currentPageIndex < pages.length - 1) {
      await goToPage(currentPageIndex + 1);
    }
  };

  /**
   * 
   */
  const translateCurrentPage = async () => {
    const currentPage = pages[currentPageIndex];

    try {
      showNotification(` ${currentPageIndex + 1} ...`, 'info');

      // 
      const response = await fetch(currentPage.image_url);
      const blob = await response.blob();

      // FormData
      const formData = new FormData();
      formData.append('image', blob, `page_${currentPageIndex + 1}.png`);
      formData.append('from_lang', 'zh');
      formData.append('to_lang', 'en');
      formData.append('save_image', 'false'); // regions

      // JWT token
      const token = localStorage.getItem('auth_token');

      // API
      const translateResponse = await fetch('/api/api-translate', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`
        },
        body: formData
      });

      if (!translateResponse.ok) {
        throw new Error('');
      }

      const data = await translateResponse.json();

      if (data.error_code && data.error_code !== 0 && data.error_code !== '0') {
        throw new Error(data.error_msg || '');
      }

      // regions
      const content = data.data?.content || [];

      if (!content || content.length === 0) {
        showNotification('', 'error');
        return;
      }

      // regions
      const regions = content.map((item, idx) => ({
        id: idx,
        src: item.src || '',
        dst: item.dst || '',
        points: item.points || [],
        lineCount: item.lineCount || 1
      }));

      // regions
      pageEditsRef.current[currentPageIndex] = {
        ...pageEditsRef.current[currentPageIndex],
        regions: regions,
        hasEdits: true
      };

      showNotification(`  ${currentPageIndex + 1}  ${regions.length} `, 'success');

      // 
      setRefreshKey(prev => prev + 1);

    } catch (error) {
      console.error(':', error);
      showNotification(': ' + error.message, 'error');
    }
  };

  /**
   * PDF
   */
  const exportPDF = async () => {
    try {
      // 1. 
      await saveCurrentPage();
      showNotification('...', 'info');

      const token = localStorage.getItem('auth_token');

      // 2. 
      const pageImages = [];

      for (let idx = 0; idx < pages.length; idx++) {
        const editData = pageEditsRef.current[idx];

        if (editData.hasEdits && editData.finalBlob) {
          // 
          const formData = new FormData();
          formData.append('page_number', idx + 1);
          formData.append('final_image', editData.finalBlob, `final_page_${idx + 1}.png`);
          formData.append('pdf_session_id', pdfData.pdf_session_id);

          const uploadResponse = await fetch('/api/pdf/save-page-edit', {
            method: 'POST',
            headers: {
              'Authorization': `Bearer ${token}`
            },
            body: formData
          });

          if (!uploadResponse.ok) {
            throw new Error(` ${idx + 1} `);
          }

          const uploadData = await uploadResponse.json();

          // 
          pageImages.push({
            page_number: idx + 1,
            image_path: uploadData.image_path,
            has_edits: true
          });

          showNotification(` ${idx + 1} `, 'info');
        } else {
          // 
          pageImages.push({
            page_number: idx + 1,
            image_path: pages[idx].image_path,
            has_edits: false
          });
        }
      }

      // 3. API
      showNotification('PDF...', 'info');

      const mergeResponse = await fetch('/api/pdf/merge-pages', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({
          pdf_session_id: pdfData.pdf_session_id,
          page_images: pageImages
        })
      });

      if (!mergeResponse.ok) {
        throw new Error('PDF');
      }

      const mergeData = await mergeResponse.json();

      if (mergeData.success) {
        showNotification(' PDF...', 'success');

        // 4. PDF - fetchblob
        const downloadUrl = mergeData.pdf_url;

        const pdfResponse = await fetch(downloadUrl, {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });

        if (!pdfResponse.ok) {
          throw new Error('PDF');
        }

        const pdfBlob = await pdfResponse.blob();
        const blobUrl = window.URL.createObjectURL(pdfBlob);

        const link = document.createElement('a');
        link.href = blobUrl;
        link.download = `edited_${pdfData.pdf_filename}`;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);

        // blob URL
        setTimeout(() => window.URL.revokeObjectURL(blobUrl), 100);

        console.log(' PDF:', downloadUrl);
      } else {
        throw new Error(mergeData.error || '');
      }
    } catch (error) {
      console.error(':', error);
      showNotification(': ' + error.message, 'error');
    }
  };

  /**
   * 
   */
  const reset = () => {
    // eslint-disable-next-line no-restricted-globals
    if (window.confirm('')) {
      setPdfData(null);
      setPages([]);
      setCurrentPageIndex(0);
      pageEditsRef.current = {};
    }
  };

  // 
  if (!pdfData) {
    return (
      <div className="pdf-test-upload">
        <div className="upload-container">
          <div className="upload-icon"></div>
          <h1>PDF</h1>
          <p>PDF</p>

          <div
            className="upload-area"
            onDragOver={(e) => {
              e.preventDefault();
              e.currentTarget.classList.add('dragover');
            }}
            onDragLeave={(e) => {
              e.currentTarget.classList.remove('dragover');
            }}
            onDrop={(e) => {
              e.preventDefault();
              e.currentTarget.classList.remove('dragover');
              const files = e.dataTransfer.files;
              if (files.length > 0) {
                handleFileUpload(files[0]);
              }
            }}
          >
            <p> PDF</p>
            <p style={{ fontSize: '14px', color: '#999', margin: '10px 0' }}></p>
            <input
              type="file"
              accept=".pdf"
              onChange={(e) => {
                if (e.target.files.length > 0) {
                  handleFileUpload(e.target.files[0]);
                }
              }}
              style={{ display: 'none' }}
              id="pdfFileInput"
            />
            <button
              className="btn-primary"
              onClick={() => document.getElementById('pdfFileInput').click()}
              disabled={isUploading}
            >
              
            </button>
          </div>

          {isUploading && (
            <div className="upload-progress">
              <div className="spinner"></div>
              <p>{uploadProgress}</p>
            </div>
          )}
        </div>
      </div>
    );
  }

  const currentPage = pages[currentPageIndex];
  const currentEdit = pageEditsRef.current[currentPageIndex];

  return (
    <div className="pdf-test-editor">
      {/*  */}
      {notification && (
        <div className={`notification ${notification.type}`}>
          {notification.message}
        </div>
      )}

      {/*  */}
      <div className="toolbar">
        <div className="toolbar-left">
          <h1> PDF</h1>
          <div className="page-info">
             {currentPageIndex + 1} / {pages.length} 
            {currentEdit.hasEdits && (
              <span className="edited-badge"></span>
            )}
          </div>
        </div>
        <div className="toolbar-right">
          <button className="btn-secondary" onClick={translateCurrentPage}>
             
          </button>
          <button className="btn-success" onClick={exportPDF}>
             PDF
          </button>
          <button className="btn-secondary" onClick={reset}>
             
          </button>
        </div>
      </div>

      {/*  - PreviewSection */}
      <div className={previewStyles.llmImageTranslationView}>
        <div className={previewStyles.llmEditorSection}>
          <div className={previewStyles.llmEditorHeader}>
            <div>
              <h2 className={previewStyles.llmEditorTitle}></h2>
              <p className={previewStyles.sectionDescription}>
                
              </p>
            </div>
            <button className={previewStyles.saveEditButton} onClick={saveCurrentPage}>
              
            </button>
          </div>

          <div className={previewStyles.llmEditorContent}>
            <FabricImageEditor
              key={`page-${pdfData.pdf_session_id}-${currentPageIndex}-${refreshKey}`}
              imageSrc={currentPage.image_url}
              regions={currentEdit.regions || []}
              editorKey={`pdf-${pdfData.pdf_session_id}-page-${currentPageIndex}-${refreshKey}`}
              exposeHandlers={true}
              onExport={() => {}}
            />
          </div>
        </div>
      </div>

      {/*  */}
      <div className="bottom-nav">
        <button
          className="nav-btn"
          onClick={previousPage}
          disabled={currentPageIndex === 0}
        >
           
        </button>

        <div className="page-indicator">
          {pages.map((_, idx) => (
            <button
              key={idx}
              className={`page-dot ${idx === currentPageIndex ? 'active' : ''} ${pageEditsRef.current[idx]?.hasEdits ? 'edited' : ''}`}
              onClick={() => goToPage(idx)}
              title={` ${idx + 1} `}
            >
              {idx + 1}
            </button>
          ))}
        </div>

        <button
          className="nav-btn"
          onClick={nextPage}
          disabled={currentPageIndex === pages.length - 1}
        >
           
        </button>
      </div>
    </div>
  );
};

export default PDFTestPage;
