import React, { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useApp } from '../contexts/AppContext';
import { authAPI } from '../services/api';
import styles from './AuthPage.module.css';

const SignUpPage = () => {
  const navigate = useNavigate();
  const { actions } = useApp();
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    confirmPassword: ''
  });
  const [loading, setLoading] = useState(false);

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (formData.password !== formData.confirmPassword) {
      actions.showNotification('', '', 'error');
      return;
    }

    setLoading(true);

    try {
      // API
      const response = await authAPI.signup(formData.name, formData.email, formData.password);

      actions.setUser(response.user);
      actions.showNotification('', '', 'success');
      navigate('/dashboard');
      
    } catch (error) {
      actions.showNotification('', error.message || '', 'error');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className={styles.authPage}>
      <button className={styles.backBtn} onClick={() => navigate('/')}>
         
      </button>
      
      <div className={styles.authContainer}>
        <div className={styles.authHeader}>
          <h2 className={styles.authTitle}></h2>
          <p className={styles.authSubtitle}></p>
        </div>
        
        <form onSubmit={handleSubmit}>
          <div className={styles.formGroup}>
            <label className={styles.formLabel} htmlFor="name"></label>
            <input
              type="text"
              id="name"
              name="name"
              className={styles.formInput}
              placeholder=""
              value={formData.name}
              onChange={handleChange}
              required
            />
          </div>
          
          <div className={styles.formGroup}>
            <label className={styles.formLabel} htmlFor="email"></label>
            <input
              type="email"
              id="email"
              name="email"
              className={styles.formInput}
              placeholder=""
              value={formData.email}
              onChange={handleChange}
              required
            />
          </div>
          
          <div className={styles.formGroup}>
            <label className={styles.formLabel} htmlFor="password"></label>
            <input
              type="password"
              id="password"
              name="password"
              className={styles.formInput}
              placeholder=""
              value={formData.password}
              onChange={handleChange}
              required
            />
          </div>
          
          <div className={styles.formGroup}>
            <label className={styles.formLabel} htmlFor="confirmPassword"></label>
            <input
              type="password"
              id="confirmPassword"
              name="confirmPassword"
              className={styles.formInput}
              placeholder=""
              value={formData.confirmPassword}
              onChange={handleChange}
              required
            />
          </div>
          
          <button 
            type="submit" 
            className={styles.submitBtn}
            disabled={loading}
          >
            {loading ? '...' : ''}
          </button>
        </form>
        
        <div className={styles.authSwitch}>
          <Link to="/signin"></Link>
        </div>
      </div>
    </div>
  );
};

export default SignUpPage;





